import React from 'react'
import { Col, Row, Image, Container } from 'react-bootstrap'
import './styles/home.css'

import pic from './resources/me.jpg'

function Home() {
    return(
        <div className='home-container'>
            <Container>
                <Row>
                    <Col className='picture'>
                        <Image src={pic} roundedCircle />
                    </Col>
                    <Col>
                        <p> 
                            <b>Hello, my name is Nathan Safir. </b>
                            I was born in Chicago (go 'hawks!), but grew up in Kansas City. When I was 16, my family moved to 
                            the Netherlands, and I later graduated high school from the American School of the Hague. From there, I went to the 
                            University of Georgia as a Foundation Fellow.
                            At UGA, I am pursuing majors in computer science and geography and a master's in artificial intelligence.
                            I am especially interested in machine learning and subtopics including deep learning, generative modeling, and representational learning. I hope to have a 
                            career where I can work on using machine learning for social good.
                        </p>
                        <p>
                            Outside of academics, you can find me skating in a pick-up hockey team, playing trivia
                            with my roomates, or meticulously ranking each restaurant in Athens. 
                            If you would like to chat, please reach out to me at <a href="mailto:nssafir@gmail.com">nssafir@gmail.com</a>.
                        </p>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Home