import React from 'react'
import { Container, Row} from 'react-bootstrap'
import { faGithubSquare, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faEnvelopeSquare } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function Footer() {
    return (
      <Container fluid className='footer'>
        <Row className='contacts'>
          <a href="https://www.linkedin.com/in/nathan-safir/" target="_blank"><FontAwesomeIcon icon={faLinkedin} className='contact' /></a>
          <a href="https://github.com/nssafir" target="_blank"><FontAwesomeIcon icon={faGithubSquare} className='contact' /></a>
          <a href="mailto:nssafir@gmail.com" target="_blank"><FontAwesomeIcon icon={faEnvelopeSquare} className='contact' /></a>
        </Row>
      </Container>
    )
}

export default Footer;
