import React from 'react'
import { Container, Col } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import './styles/about.css'

function About() {
    return(
        <Container className='about-container'>
            <Col>
                <h3 className = 'about-title'>hockey</h3>
                <p>
                    I started playing hockey when I was 9, and to this day it is one 
                    of my favorite activities. I played defenseman for a club team
                    growing up in Kansas and in high school. My senior year, I played on a Dutch amateur team with
                    my father. Though I no longer play competively, I now help coach a youth
                    team in Athens and play pick up whenever I get the chance.
                </p>
            </Col>
            <Col>
                <h3 className = 'about-title'>travel</h3>
                <p>
                    After moving to Europe towards the end of high school, I was able to 
                    visit cities in Europe I never thought I would see. The Foundation Fellowship
                    has also provided me many travel opportunities, although travelling is certainly
                    on-hold now due to COVID-19. 
                </p>
            </Col>
            <Col>
                <h3 className = 'about-title'>reading</h3>
                <p>
                    Recently, I have been trying to read more. I enjoy reading a mix of fiction
                    and non-fiction. Check out my <a href='https://www.goodreads.com/user/show/105551716-nathan-safir'>
                    Goodreads profile</a> if you want to know more.
                </p>
            </Col>
        </Container>
    )
}

export default About