import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Accordion } from 'react-bootstrap'
import './styles/timeline.css'

import google from './resources/google.png'
import qrg from './resources/qrg.png'
import delug from './resources/delug.jpg'
import tno from './resources/tno.png'
import uga from './resources/uga.png'
import oxford from './resources/oxford.png'


function Timeline() {
    return (
        <div className='timeline-container'>
            <Accordion className='timeline'>
                {items.map((data) => (
                    <TimelineItem data={data} />
                ))} 
            </Accordion>
        </div>
    )
}

const TimelineItem = ({ data }) => (
    <div className='timeline-item'>
      <div className='title-container'>
        <span className='circle' />
        <h4>{data.org}</h4>
        <img src={data.img} className='logo' alt='logo' />
      </div>
      <h5>{data.role} | {data.location} | {data.date}</h5>
      <div className='timeline-item-content'>
        <div>{data.description}</div>
      </div>
    </div>
);

const items = [
    {
        org: 'Google',
        role: 'STEP Intern',
        location: 'Mountain View (remote), California',
        date: 'March 2020 - August 2020',
        description: 
        <div>
            I worked with two other STEP interns on the Counter Abuse Team to develop Engage, 
            an open-source web application which helps users engage with their social media feed 
            by using NLP techniques to find relevant books, news articles, charitable projects, and
            civic engagement opportunities. I implemented the books, news, and location integration 
            features of the project through full-stack develpment, web scraping, and the use of 5 Google
            API's. Additionally, I learned web development through creating a starter portfolio with
            Java, Javascript, HTML, and CSS, and completed internal Google machine learning modules
            and participated in a Deep Learning Reading Group. 
        </div>,
        img: google
    },
    {
        org: 'Quinn Research Group',
        role: 'Undergraduate Research Assistant',
        location: 'Athens, Georgia',
        date: 'January 2019 - Present',
        description: 
        <div>
            I am a researcher for the generative models team within Quinn Research Group. 
            Currently, we are working on implementing a novel, variational autoencoder-based pipeline
            to generate meaningful manifold representations for cilia video data. The goal of this project is
            to help clinicians better identify and understand how some diseases impact ciliary motion.
            We have had a paper accepted into SciPy 2020 conference and have presented our work at SciPy 2020,
            Southern Data Science Conference 2020, and the Advancing Informatics in Government and Industry conference in 2019.
            I have also presented my individual work at UGA's CURO conference 
            and have been granted the CURO Assistantship twice for excellence in research.
        </div>,
        img: qrg
    },
    {
        org: 'Deep Learning at UGA (DELUG)',
        role: 'Workshop Co-Chair',
        location: 'Athens, Georgia',
        date: 'January 2020 - Present',
        description: 
        <div>
            My role at DELUG is to develop and present workshops to teach deep learning to the UGA community.
            So far, we have presented to hundreds of students and faculty and have created a series of virtual
            workshops. I also serve on the executive board, making strategic decisions for the organization and
            recruiting guest speakers.
        </div>,
        img: delug
    },
    {
        org: 'Netherlands Organisation for Applied Science Research (TNO)',
        role: 'Machine Learning Intern',
        location: 'Delft, The Netherlands',
        date: 'June 2019 - August 2019',
        description: 
        <div>
            I was tasked with creating a model to differentiate between high-frequency neutrino signals
            and underwater dolphin harmonics for a large international particle physics experiment (KM3NeT). 
            I developed a pipeline using signal processing algorithms and convolutional neural network models
            to classify raw audio signals from hydrophone data. 
            I achieved over 99% accuracy in differentiating between dolphin clicks and simulated neutrino signals.
        </div>,
        img: tno
    },
    {
        org: 'University of Oxford',
        role: 'Maymester Student',
        location: 'Oxford, England',
        date: 'May 2019 - June 2019',
        description: 
        <div>
            After my freshman year, I had the opportunity to study in Oxford for the Maymester academic period.
            In the course Computing Ethics and Society, I studied the societal implications of technology and A.I. 
            in a rigorous, essay-based tutorial curriculum under Oxford faculty. The program was fully funded through the Foundation Fellowship.
        </div>,
        img: oxford
    },
    {
        org: 'University of Georgia Department of Computer Science',
        role: 'Undergraduate Student',
        location: 'Athens, Georgia',
        date: 'August 2018 - Present',
        description: 
        <div>
            I am currently a junior at the University of Georgia, pursuing majors in computer science and geography
            and a master's in artificial intelligence, aiming to graduate by Spring 2022. I am taking or have taken
            courses in machine learning (grad. level), data mining (grad. level), human computer interaction (grad. level),
            data structures, theory of computation, systems programming, and software development. Some courses outside the 
            computer science department I have enjoyed are data science in geography, intro to G.I.S., and honors
            multivariable mathematics. Course projects are kept private for academic honesty reasons, but can be made
            available to employers upon request.
        </div>,
        img: uga
    },
]

export default Timeline;