import React from 'react';
import { Container, Row, Card } from 'react-bootstrap';
import './styles/writing.css'

import gov from './resources/gov.jpg'
import chopped from './resources/chopped.jpeg'
import gifted from './resources/gifted.jpg'
import hockey from './resources/hockey.jpg'

function Writing() {
    return(
        <Container className='writing-container'>
            <Row>
                <p>
                    In addition to working on more technical projects, I enjoy writing. 
                    Currently, I serve as the editor-in-chief for the 
                    <a href='http://georgiapoliticalreview.com/'> Georgia Political Review</a>,
                    where I have written and edited for the past two years. Previously, I wrote
                    for a blog called FreshU. I am also working on an article with a geography professor
                    on hauntology, nostalgia, and the American corporation. 
                    Below are some articles I have written.
                </p>
            </Row>
            <Row>
                {
                    articles.map((article) => (
                        <Card className='card-container'>
                            <Card.Img fluid variant='top' src={article.img} className='card-img'/>
                            <Card.Body>
                                <Card.Title className='card-title'>{article.title}</Card.Title>
                                <Card.Text>{article.description}</Card.Text>
                                <div className='button'><a href={article.link}>Read More</a></div>
                            </Card.Body>
                        </Card>
                    ))
                }
            </Row>
        </Container>
    );
}

const articles = [
    {
        title: "Regulating Artificial Intelligence",
        description: "On federal efforts to legislate the development of A.I.",
        img: gov,
        link: "http://georgiapoliticalreview.com/legislating-artificial-intelligence/"
    },
    {
        title: "The Politics of Chopped",
        description: "On the cooking show Chopped and the gig economy",
        img: chopped,
        link: "http://georgiapoliticalreview.com/the-politics-of-chopped/"
    },
    {
        title: "The Rise of The Gifted Kid Candidate",
        description: "On the American mythos of gifted education",
        img: gifted,
        link: "http://georgiapoliticalreview.com/the-rise-of-the-gifted-kid-candidate/"
    },
    {
        title: "Keeping Hockey in Check",
        description: "On Don Cherry, Spittin' Chiclets, and the line between content and journalism",
        img: hockey,
        link: "http://georgiapoliticalreview.com/pseudo-journalism-and-the-politics-of-hockey-punditry/"
    }
]

export default Writing;