import React from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import './styles/navbar.css'

import resume from './resources/resume.pdf'

function NavBar() {
    return(
        <Navbar class='navbar' sticky='top'>
            <Navbar.Brand href='#home'>
                <div className='navlink'>Nathan Safir</div>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls='responsive-navbar-nav' />
            <Navbar.Collapse id='responsive-navbar-nav'>
                <Nav className='ml-auto'>
                    <Nav.Link href='#home'><div className='navlink'>Home</div></Nav.Link>
                    <Nav.Link href='#experience'><div className='navlink'>Experiences</div></Nav.Link>
                    <Nav.Link href='#writing'><div className='navlink'>Writing</div></Nav.Link>
                    <Nav.Link href='#interests'><div className='navlink'>Interests</div></Nav.Link>
                    <Nav.Link href='#footer'><div className='navlink'>Contact</div></Nav.Link>
                    <Nav.Link href='#resume'><div className='navlink'><a href={resume}>Resume</a></div></Nav.Link>                    
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default NavBar;